import React, { useState } from "react";
import { useDispatch } from "react-redux";
import {
  Avatar,
  Button,
  Paper,
  Grid,
  Typography,
  Container,
} from "@material-ui/core";
import { Link, useHistory } from "react-router-dom";
import { GoogleLogin } from "react-google-login";

import Icon from "./icon";
import useStyles from "./styles";
import { LockOutlined } from "@material-ui/icons";
import { Input } from "..";
import {
  formSignin,
  formSignup,
  setGoogleAuth,
} from "../../features/authSlice";
import { sm } from "../../utils/screenSize";
import { useSnackbar } from "notistack";
import { snackMessages } from "../../snackMessages";

const initialState = {
  firstName: "",
  lastName: "",
  email: "",
  password: "",
  confirmPassword: "",
};

export const Auth = () => {
  const [formData, setFormData] = useState(initialState);
  const [isSignup, setIsSignup] = useState(false);
  const dispatch = useDispatch();
  const history = useHistory();
  const classes = useStyles();
  const { enqueueSnackbar } = useSnackbar();

  const [showPassword, setShowPassword] = useState(false);
  const handleShowPassword = () => setShowPassword(!showPassword);

  const switchMode = () => {
    // setForm(initialState);
    setIsSignup((prevIsSignup) => !prevIsSignup);
    setShowPassword(false);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const data = { formData, history };
    if (isSignup) {
      dispatch(formSignup(data));
      enqueueSnackbar(snackMessages.signin, {
        variant: "success",
      });
    } else {
      dispatch(formSignin(data));
      enqueueSnackbar(snackMessages.signup, {
        variant: "success",
      });
    }
  };

  const googleSuccess = async (res) => {
    const result = await res?.profileObj;
    const token = await res?.tokenId;
    // console.log(result);
    // console.log(token);
    try {
      const data = { result, token };
      dispatch(setGoogleAuth(data));
      history.push("/");
      enqueueSnackbar(snackMessages.googleSuccess, {
        variant: "success",
      });
    } catch (error) {
      console.log(error);
    }
  };

  const googleError = () => {
    enqueueSnackbar(snackMessages.googleError, {
      variant: "error",
    });
  };
  const handleChange = (e) =>
    setFormData({ ...formData, [e.target.name]: e.target.value });

  return (
    <Container component="main" maxWidth="xs" disableGutters={sm && true}>
      <Paper className={classes.paper} elevation={3}>
        <Avatar className={classes.avatar}>
          <LockOutlined />
        </Avatar>
        <Typography component="h1" variant="h5">
          {isSignup ? "Sign up" : "Sign in"}
        </Typography>
        <form className={classes.form} onSubmit={handleSubmit}>
          <Grid container spacing={2}>
            {isSignup && (
              <>
                <Input
                  name="firstName"
                  label="First Name"
                  handleChange={handleChange}
                  autoFocus
                  half
                />
                <Input
                  name="lastName"
                  label="Last Name"
                  handleChange={handleChange}
                  half
                />
              </>
            )}
            <Input
              name="email"
              label="Email Address"
              handleChange={handleChange}
              type="email"
            />
            <Input
              name="password"
              label="Password"
              handleChange={handleChange}
              type={showPassword ? "text" : "password"}
              handleShowPassword={handleShowPassword}
            />
            <label htmlFor="password" style={{ marginLeft: "10px" }}>
              <Link
                to="/forgotpassword"
                className="login-screen__forgotpassword"
              >
                Forgot Password?
              </Link>
            </label>
            {isSignup && (
              <Input
                name="confirmPassword"
                label="Repeat Password"
                handleChange={handleChange}
                type="password"
              />
            )}
          </Grid>
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
          >
            {isSignup ? "Sign Up" : "Sign In"}
          </Button>
          <GoogleLogin
            clientId="564842978452-tm3hgg3ff5m9k2olbtmvap0m2skag09e.apps.googleusercontent.com"
            onSuccess={googleSuccess}
            onFailure={googleError}
            cookiePolicy="single_host_origin"
            uxMode="popup"
            render={(renderProps) => (
              <Button
                className={classes.googleButton}
                color="primary"
                fullWidth
                onClick={renderProps.onClick}
                disabled={renderProps.disabled}
                startIcon={<Icon />}
                variant="contained"
              >
                Google Sign In
              </Button>
            )}
          />
          <Grid container justify="flex-end">
            <Grid item>
              <Button onClick={switchMode}>
                {isSignup
                  ? "Already have an account? Sign in"
                  : "Don't have an account? Sign Up"}
              </Button>
            </Grid>
          </Grid>
        </form>
      </Paper>
    </Container>
  );
};
