import React from "react";
import {
  Card,
  CardActions,
  CardContent,
  CardMedia,
  Button,
  Typography,
  // IconButton,
} from "@material-ui/core/";
import moment from "moment";
import useStyles from "./styles";
import {
  DeleteOutlined,
  Favorite,
  FavoriteBorder,
  FavoriteBorderOutlined,
  MoreHorizOutlined,
} from "@material-ui/icons";
import { useDispatch } from "react-redux";
import { setModal } from "../../../features/post/modalSlice";
import {
  deletePost,
  likePost,
  setCurrentId,
} from "../../../features/post/postsSlice";
import { useSnackbar } from "notistack";
import { option, snackMessages } from "../../../snackMessages";

export const Post = ({ post }) => {
  const classes = useStyles(),
    dispatch = useDispatch(),
    { enqueueSnackbar } = useSnackbar(),
    user = JSON.parse(localStorage.getItem("profile"));

  const postEdit = () => {
    const currentUserID = user?.result?.googleId || user?.result._doc._id;
    if (user) {
      if (post._id !== currentUserID) {
        return enqueueSnackbar(snackMessages.unauthorized, option("error"));
      }
    } else {
      enqueueSnackbar(snackMessages.isUser, option("info"));
    }
    dispatch(setModal(true));
    dispatch(setCurrentId(post._id));
  };

  const currentUserDelActions = (action = "") => {
    const currentUserID = user?.result?.googleId || user?.result?._doc._id;
    if (user) {
      currentUserID === post?.creator
        ? action()
        : enqueueSnackbar(snackMessages.unauthorized, option("error"));
    } else enqueueSnackbar(snackMessages.isUser, option("info"));
  };

  const currentUserLikeActions = (action) => {
    user ? action() : enqueueSnackbar(snackMessages.isUser, option("info"));
  };

  const Likes = () => {
    if (post.likes.length > 0) {
      return post.likes.find(
        (like) => like === (user?.result?.googleId || user?.result?._id)
      ) ? (
        <>
          <Favorite fontSize="small" />
          &nbsp;
          {post.likes.length > 2
            ? `You and ${post.likes.length - 1} others`
            : `${post.likes.length} like${post.likes.length > 1 ? "s" : ""}`}
        </>
      ) : (
        <>
          <Favorite fontSize="small" />
          &nbsp;{post.likes.length} {post.likes.length === 1 ? "Like" : "Likes"}
        </>
      );
    }

    return (
      <>
        <FavoriteBorderOutlined fontSize="small" />
        &nbsp;Like
      </>
    );
  };
  const del = () => dispatch(deletePost({ id: post._id }));
  const fav = () => dispatch(likePost({ id: post._id }));

  return (
    <Card className={classes.card}>
      <CardMedia
        className={classes.media}
        image={
          post.selectedFile ||
          "https://user-images.githubusercontent.com/194400/49531010-48dad180-f8b1-11e8-8d89-1e61320e1d82.png"
        }
        title={post.title}
      />
      <div className={classes.overlay}>
        <Typography variant="h2" className={classes.smallHeading}>
          {post.name}
        </Typography>
        <Typography variant="body2">
          {moment(post.createdAt).fromNow()}
        </Typography>
      </div>
      <div className={classes.overlay2}>
        <Button
          aria-label="edit"
          style={{ color: "white" }}
          size="small"
          onClick={postEdit}
        >
          <MoreHorizOutlined fontSize="default" />
        </Button>
      </div>
      <div className={classes.details}>
        <Typography variant="body2" color="textSecondary" component="h2">
          {post.tags.map((tag) => `#${tag} `)}
        </Typography>
      </div>
      <Typography
        className={classes.title}
        gutterBottom
        variant="h5"
        component="h2"
      >
        {post.title}
      </Typography>
      <CardContent>
        <Typography variant="body2" color="textSecondary" component="p">
          {post.message}
        </Typography>
      </CardContent>
      <CardActions className={classes.cardActions}>
        <Button
          size="small"
          className={classes.btnColor}
          onClick={
            // fav
            (e) => {
              e.preventDefault();
              currentUserLikeActions(fav);
            }
          }
        >
          <Likes />
        </Button>
        <Button
          size="small"
          className={classes.btnColor}
          onClick={(e) => {
            e.preventDefault();
            currentUserDelActions(del);
          }}
        >
          <DeleteOutlined fontSize="small" /> Delete
        </Button>
      </CardActions>
    </Card>
  );
};
